.header {
  background-color: rgba(0, 255, 255, 0.432);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 30px;
}

.logo {
  color: rgb(59, 59, 59);
  font-size: 28px;
}

.nav-container {
  display: flex;
}

.nav-item {
  list-style: none;
  margin: 0px 20px;
}

.nav-item a {
  color: white;
  text-decoration: none;
  background-color: rgb(70, 188, 209);
  padding: 10px 20px;
  border-radius: 20px;
  font-size: 17px;
}

.home a:hover,
.explore a:hover {
  background-color: steelblue;
}

.logout a:hover {
  background-color: burlywood;
}
