form {
  width: 50%;
  background-color: rgba(125, 125, 214, 0.63);
  margin: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 30px;
  border-radius: 20px;
}

.input-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 50%;
  margin-bottom: 20px;
}

label {
  color: white;
  font-size: 20px;
}

input {
  background: none;
  width: 70%;
  height: 50px;
  border: 2px solid white;
  border-radius: 10px;
  padding: 0px 10px;
  color: white;
}

input:focus {
  outline: none;
}

button {
  padding: 10px 30px;
  color: white;
  background-color: rgba(95, 158, 160, 0.514);
  font-size: 20px;
  border: none;
  border-radius: 20px;
  cursor: pointer;
}

button:disabled,
button:focus:disabled,
button:hover:disabled,
button:active:disabled {
  background: #ccc;
  border-color: #ccc;
  color: #666666;
  cursor: not-allowed;
}
